.worksBlock {
    height: auto;
    padding: 100px 0;
    background-color: #f9ebfd;
    display: flex;
    justify-content: center;
    align-items: center;
}

.worksContainer {
    /*border: 1px solid red;*/
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

